let sideBar = $('#sideBar');
let mask1 = $('#mask1');
let sortBar = $('#sortBar');

$('#icoFilter').on('click', function() {
	mask1.toggle();
	sideBar.toggleClass('shown');
});

$('#icoCart').on('click', function() {
	mask1.toggle();
	sortBar.toggleClass('shown');
});

mask1.on('click', function() {
	sideBar.removeClass('shown');
	sortBar.removeClass('shown');
	mask1.hide();
});

let slippyList = document.querySelector('#slippyList');
slippyList.addEventListener(
	'slip:beforewait',
	function(e) {
		e.preventDefault();
	},
	false
);

slippyList.addEventListener(
	'slip:beforereorder',
	function(e) {
		if (/ico-remove/.test((e.target as HTMLElement).className)) {
			e.preventDefault();

			var btnArray = [lngPkg.okb, lngPkg.cancelb];
			mui.confirm(lngPkg.Content, lngPkg.Tips, btnArray, function(ev) {
				if (ev.index == 0) {
					api.unlike({ buyerBarcode: $(e.target).data('barcode') }, showMyList);
				}
			});			
		}
	},
	false
);

slippyList.addEventListener(
	'slip:reorder',
	function(e: any) {
		(e.target as Element).parentNode.insertBefore(e.target as Element, e.detail.insertBefore);
		updateSort();
		return false;
	},
	false
);

slippyList.addEventListener(
	'slip:beforeswipe',
	function(e) {
		if (/ico-remove/.test((e.target as HTMLElement).className)) {
			e.preventDefault();
		}
	},
	false
);

