new Slip(slippyList);

showMyList();

let renderChoosed = template.compile($('#tpl-choosed').html());

function showMyList() {
	api.myList({ scheduleAddtoTime: scheduleAddtoTime }, data => {
		slippyList.innerHTML = renderChoosed(data);
	});
}

function updateSort() {
	let buyerBarCodes = [];
	$('li', slippyList).each((i, li: HTMLLIElement) => {
		buyerBarCodes.push(li.getAttribute('data-barcode'));
	});

	api.updateListSort({ buyerBarCodes }, function() {});
}

let divResult = $('#divResult');
let renderResult = template.compile(document.getElementById('tpl-result').innerHTML);

let tplDetail = template.compile(document.getElementById('tpl-detail').innerHTML);

function addBuyer(buyerBarcode) {
	api.likeBuyerForExhibtionByBarCodeInList({ buyerBarcode }, data => {
		showMyList();
		mui.toast(Languages.package.addWishListOk, { verticalAlign: 'center' });
	});
}

divResult.on('click', 'li', function(e) {
	let toBarCode = this.getAttribute('data-barcode');

	if (e.target.tagName === 'IMG') {
		addBuyer(toBarCode);
		return false;
	}

	let mask = $(`<div class="mask" style="background-color: rgba(0,0,0,0.6)"></div>`);
	mask.on('click', function() {
		mask.remove();
	});

	let obj = data[this.getAttribute('data-index')];
	obj.fromSchedule = fromSchedule;
	obj.stage = wishListStage;
	obj['__lng'] = Languages.package;
	obj.currentLng = currentLng;
	mask.append(tplDetail(obj));
	$('body').append(mask);
	console.log(obj);
	//data-barcode

	$('#btnSendSchedule').click(function(e) {
		e.stopPropagation();
		api.sendScheduleMessage(
			{
				msgTitle: Languages.package.msgTitle,
				msgContent: request['info'].replace(/\+/gi, ' '),
				toBarCode
			},
			data => {
				mask.remove();
				mui.alert(Languages.package.scheduleOk, Languages.package.tipTitle, Languages.package.okb, () => {
					window.location.href = '../schedule/mine.html';
				});
			}
		);
	});

	$('#btnWishList').click(function(e) {
		e.stopPropagation();
		mask.remove();
		addBuyer(toBarCode);
	});
});

const alpha = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
let list: any;
let data;

function showData(arr) {
	list = {};
	if (Languages.current === 'en') {
		data = array.sort(arr, 'company', function(a, b) {
			let A = a['company'].toUpperCase(),
				B = b['company'].toUpperCase();
			return A === B ? 0 : A > B ? 1 : -1;
		});
		for (let i = 0, l = data.length; i < l; i++) {
			let it = data[i],
				f = it['company'].substr(0, 1).toUpperCase();
			if (!list[f]) {
				if (alpha.indexOf(f) === -1) f = '#';
				list[f] = [];
			}
			it[':index'] = i;
			list[f].push(it);
		}
	} else {
		data = array.sort(arr, 'company', function(a, b) {
			let A = makePy(a['company'])[0].toUpperCase(),
				B = makePy(b['company'])[0].toUpperCase();
			return A === B ? 0 : A > B ? 1 : -1;
		});
		for (let i = 0, l = data.length; i < l; i++) {
			let it = data[i],
				f = makePy(it['company'])[0]
					.substr(0, 1)
					.toUpperCase();

			if (!list[f]) {
				if (alpha.indexOf(f) === -1) f = '#';
				list[f] = [];
			}
			it[':index'] = i;
			list[f].push(it);
		}
	}

	divResult.html(renderResult({ list, stage: wishListStage }));
}

//api.search({"keyword":"","region":"","product":""} , data => showData(data));

function doSearch(){
	let param = $('#sideBar').fieldsToJson();
	api.search(param, data => showData(data));
}
document.getElementById('btnSearch').addEventListener(
	'click',
	function() {
		sideBar.removeClass('shown');
		mask1.hide();
		doSearch();
	},
	false
);

document.getElementById('btnReset').addEventListener(
	'click',
	function() {
		$("#selRegion option:first").prop("selected", 'selected'); 
		$("#selProduct option:first").prop("selected", 'selected'); 	
		$("#iptSearch").val("");
	},
	false
);



const toggleViewStyle = document.querySelector('#toggleViewStyle');
let currentStyle = Store.get('wishListViewStyle', 'list');
tkView(currentStyle);

$('#toggleViewStyle').click(function (e) {
	if((e.target as HTMLElement).className === 'i-icon'){
		return false;
	}
	currentStyle = currentStyle === 'thumb' ? 'list' : 'thumb';
	tkView(currentStyle);
});

function tkView(style: string) {
	divResult[0].className = (currentStyle === 'list' ? 'divListView' : 'divThumbView');
	toggleViewStyle.className = currentStyle;
	Store.set('wishListViewStyle', currentStyle);
}


if (fromSchedule) {
	$('#backurl').attr('href', '../schedule/mine.html');
} else {
	$('#backurl').attr('href', '../dashboard/index.html');
}

